.jumbotron {
    background-image: url('https://bit.ly/2SpVJcC');
    background-repeat: no-repeat;
    background-size: 100vw;
    height:600px;
    border-radius:0;
   
}

.jumboText {
    color:rgba(250,250,250,.75); 
    text-align: center;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
    font-family: 'Nunito', sans-serif;
    font-size: 8rem;


}

.jumboSubText{
    color:rgba(250,250,250,.75); 
    text-align: center;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
    font-family: 'Nunito', sans-serif;
    font-size: 2.5rem;

 
}

hr{
    width:15rem;
    background-color:white;
}

@media only screen and (max-width: 600px) {
    .jumbotron {
        background-image: url('https://bit.ly/2SpVJcC');
        background-repeat: no-repeat;
        background-size: auto;
        
       
    }
    
    .jumboText {
        color: white; 
        text-align: center;
        -webkit-transform: translateY(200px);
                transform: translateY(200px);
        font-family: 'Nunito', sans-serif;
        font-size: 5rem;


    }

    .jumboSubText{
        color: white; 
        text-align: center;
        -webkit-transform: translateY(220px);
                transform: translateY(220px);
        font-family: 'Nunito', sans-serif;
        font-size: 2rem;

    
    }
  }
  .overlay {
    position: absolute;
    bottom: -20px;
    left: 20px;
    right: 0;
    background-color: rgba(0,0,0,.75);
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: 1s ease;
  }
  
  .PathContainer:hover .overlay {
    height: 100%;
    border: none;
  }
  
  .text {
    color: white;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
    font-family: 'Nunito', sans-serif;
    border: 1px solid white;
    padding: 10px;

  }

.PathCardDesc{
    padding: 20px;
    font-size: 15px;
    height: 275px;
    width: 300px;
    font-family: 'Nunito', sans-serif;
    box-shadow: 10px 10px 175px rgba(116, 116, 116, 0.5);
    color:black;
    font-weight:bold;
    border: 1px solid rgba(0,0,0,.1);
}

img {
    background-color: white;
    height: 200px;
    width: 300px;

}

 .PathContainer {
    height: 200px;
    width: 300px;
    display: -webkit-inline-box;
    margin:50px ;
    text-align:center;
    margin-bottom: 300px;
    position: relative;
} 

.PickPathText{
    text-align:center;
    font-family: 'Nunito', sans-serif;

}

/* Modal */

.ModalBackground{
    position:fixed;
    top:0;
    bottom:0;
    left:0px;
    right:0px;
    display:flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0,0,0, .7);
    color: black;
    z-index: 1000;
}

.ModalBox{
    border: 1px solid black;
}
.navbar{
    background-color: transparent;
    color:rgba(250,250,250,.75);
    position: absolute;
    width: 100%;
    display:flex;
    font-family: 'Nunito', sans-serif;
    padding:0;

}

hr{
    background-color: white;
    width: 7rem;
}

a{
    display:inline-block;
    color:rgba(250,250,250,.75);
    font-size:1rem;
    text-decoration: none;
    padding:20px;
    
}

a:hover{
    display:inline-block;
    font-size:1rem;
    text-decoration: none;
    padding:20px;
    transition: .5s ease;

        
}

a span{
    position:relaive;
    transition: 0.5s
}

a span:after{
  
    position:absolute;
    opacity:0;
    top:0;
    font-size:1rem;
    transition: 0.5s;

}

a:hover span{
    font-size:1.5rem;

}

a:hover span:after {
    opacity:1;
    
}
